import React from "react";  
import './Color.css';

type props = {
  label: string;
  note?: string;
  value: string;
  onChange: Function;
}

/**
 * Un input de selecci贸n de colores hexadecimales.
 * @param props.label     - El t铆tulo del input.
 * @param props.note      - Nota extra acerca del input.
 * @param props.value     - El valor hexadecimal de 7 caracteres.
 * @param props.onChange  - La funci贸n a ejecutar en el evento de cambio del valor.
 */
export default function Image({label, note, value, onChange}:props) {
  return (
    <label className="color">
        {label}<span> {note}</span>
        <p>{value}</p>
        <input
          value={value}
          type="color"
          onChange={e => onChange(e.target.value)}
        />
    </label>
  )
}