import React from "react";  
import './Image.css';
import defaultAvatar from 'assets/svg/user.svg';
import { BsFillXCircleFill } from "react-icons/bs";

type props = {
    label: string;
    note?: string;
    fallback?: string;
    setter: React.Dispatch<React.SetStateAction<File>>;
    img: File;
}

/**
 * Un input de archivos de tipo im谩gen. Acepta png, jpg y svg.
 * @param props.label     - El t铆tulo del input.
 * @param props.note      - Nota adicional acerca del input.
 * @param props.fallback  - URL de im谩gen a mostrar cuando no hay ninguna otra im谩gen. Por defecto es un icono de usuario.
 * @param props.setter    - Funci贸n controladora del estado de la constante que almacena la im谩gen.
 * @param props.img       - Valor File de la im谩gen a mostrar.
 */
export default function Image({label, note, fallback=defaultAvatar, setter, img}:props) {

  return (
    <label className="image">
        {label}<span> {note}</span>
            
        {img?.size>10?
        <BsFillXCircleFill onClick={e=>{
          e.preventDefault();
          setter(undefined);
          }} /> : null}
            
        <input
          type="file"
          accept=".png, .jpeg, .jpg, .svg"
          onChange={e => {
            if (e.target.files && e.target.files.length > 0) setter(e.target.files.item(0));
          }}
        />
        <div>
        <img src={img?.size>10? URL.createObjectURL(img):fallback} />
        </div>

    </label>
  )
}